import React from "react";

class PatternInput extends React.Component{
    constructor(props){
        super(props);
        this.state = {matched : false,currentValue:this.props.value};
    }
    

    valueChanged(e){
        this.setState({currentValue : e.target.value});
        if(this.props.pattern.test(e.target.value)){
            
            this.props.onMatch(e.target.value);
            this.setState({matched : true});
        }
    }

    render(){
        let className= this.state.matched ? "pattern-input-matched" : "pattern-input-unmatched";

        return(<input value={this.state.currentValue} placeholder={this.props.placeholder} disabled={!this.props.enabled} type="text" onChange={e => this.valueChanged(e)} className={className}/>);
    }   
    
}

export default PatternInput;