import React from "react";
class ConfirmationButton extends React.Component{

    captionDisabled = "In Progress...";
    constructor(props){
        super(props);
        this.captionEnabled = this.props.captionEnabled;
    }

    render(){
        return(
            <button class="btn" disabled={!this.props.enabled} id="btn_confirm" onClick={() => this.props.onClick()}>{this.props.enabled ? this.captionEnabled : this.captionDisabled}</button>
        );
        }

}

export default ConfirmationButton