import React from "react";
import * as api from './api/api';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Tab } from "@mui/material";

function formatTime(timestamp){
    let date = new Date(timestamp * 1000);
    let formatedDate =
        date.getDate() + "." +
        (date.getMonth()+1) + "." +
        date.getFullYear() + " - " +
        date.getHours() + ":" +
        date.getMinutes() + ":" +
        date.getSeconds()
        ;
    return formatedDate;

}

class JobRow extends React.Component{
    constructor(props){
        super(props);
        this.row = this.props.row;
    }    

    state = {render : true};

    removeChild(){
        let confirmed = window.confirm("Do you realy want remove this job?");
        if(confirmed){
            api.removeJob(this.row.id, () => this.setState({render : false }));
        }
    }


    render(){
            console.log("Job Row");
            if(this.state.render){
                return(
                    <TableRow
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                    <TableCell>{formatTime(this.row.ts_start)}</TableCell>
                    <TableCell>{this.row.type}</TableCell>
                    <TableCell>{<a href={this.row.url} target="_blank">{this.row.title}</a>}</TableCell>
                    <TableCell>{api.getStatus(this.row.status)}</TableCell>
                    <TableCell>{this.row.status==api.FINISHED ? <a href={this.row.download_url}>Download here</a> : ""}</TableCell>
                    <TableCell><span onClick={(e) => this.removeChild()} className="btn-remove">x</span></TableCell>
                    </TableRow>
                );
            }
            else{
                return null;
            }
    }



}

export default JobRow;