import React from 'react';
import * as api from './api/api';
import ConfirmationButton from './confirmationButton';



class LoginForm extends React.Component{
    password = "";
    
    constructor(props){
        super(props);
        this.state = {
          confirmationButtonEnabled : true,
          remember : true,
          success : false
        }
        
      }


      loginButtonClicked(){
        this.setState({
          confirmationButtonEnabled : false
        });
        api.login(this.password, this.state.remember, json => {
          if(json.success){
            this.setState({success : true})
          }
          else{
            alert("Wrong password!");
            this.setState({confirmationButtonEnabled : true});
          }
        });

      }

    
      render(){
        if(!this.state.success){
          return(
              <div id="loginForm">
                  <fieldset>
                  <legend>Password</legend>
                  <input type="password" size="20" onChange={(e) => this.password = e.target.value}/><br/>
                  Remember me 
                  <input type="checkbox" checked={this.state.remember} onClick={(e) => this.setState({remember : !this.state.remember})} /><br/>
                  <ConfirmationButton captionEnabled="Login" enabled={this.state.confirmationButtonEnabled} onClick={this.loginButtonClicked.bind(this)} />
                  </fieldset>
              </div>
          );
        }
        else{
         window.location.reload();
        }
    }
      
}

export default LoginForm;