import React from 'react';
import JobQueue from './JobQueue';
import * as api from './api/api';
import * as cookies from './api/CookieManagement';
import ConfirmationButton from './confirmationButton';
import PatternInput from './PatternInput'

class JobForm extends React.Component{
    url = "";
    type = "";
    mail = "";
    pattern = /^[a-zA-Z0-9\-_\.]+@[a-zA-Z0-9\-_\.]+\.[a-z]{2,}$/; //Pattern to validate mail address
    constructor(props){
        super(props);
        this.mail=cookies.getCookie("mail");
        this.state = {
          rows : [],
          url : "",
          type : "",
          confirmationButtonEnabled : true,
          useMailNotification : false
        }

        
      }

      update(){
        this.unsetTicker();
        api.getJobs(
            json => {
                let rows = [];
                for(var k in json){
                  rows.push(json[k]);
                }
                this.setState({
                  rows : []
                });
                this.setState({rows : rows});
                this.setTicker();
            }
        )
    }

    setTicker(){
        this.ticker = setInterval(() => this.update(), 10000);
    }

    unsetTicker(){
        clearInterval(this.ticker);
    }

    componentDidMount(){
        this.setTicker();
        this.update();
    }

    componentDidUnmount(){
        this.unsetTicker();
    }

    storeMailAddress(sendMail = this.state.useMailNotification){
      if(sendMail){
        api.storeMailAddress(this.mail);
      }
      else{
        api.storeMailAddress("");
      }
    }

    mailPatternMatched(mail){
      this.mail = mail;
      this.storeMailAddress();
      cookies.setCookie("mail", mail, 28);
    }

      addJobButtonClicked(){
        if(this.type===""){
          alert("Please select a format");
          return ;
        }

        for(var k in this.state.rows){
          if(this.state.rows[k].url === this.url){
            alert("This clip is aleady in your queue!");
            return ;
          }
        }
        this.unsetTicker();
        this.setState({confirmationButtonEnabled : false});
        api.addJob(
          this.url,
          this.type,
          (json) => 
          { 
            if(json.title!=null){
              this.storeMailAddress();
              alert("The download job was added sucessfully to your queue."); 
              this.setState({
                rows : this.state.rows.concat([json]),
                confirmationButtonEnabled : true});
            }
            else{
              alert("Invalid URL: " + this.url);
              this.setState({
                confirmationButtonEnabled : true
              });
            }
            this.setTicker();
          
          }
        )
       
        
      }


    
      render(){
        return(
            <div id="jobForm">
                <fieldset>
                <legend>URL</legend>
                <input type="text" id="url" onChange={(e) => this.url = e.target.value}/>
                </fieldset>
                <fieldset>
                <legend>Gew√ºnschtes Format</legend>
                Audio 
                <input type="radio" name="type" value="audio" onChange={(e) => this.type = e.target.value} />
                Video 
                <input type="radio" name="type" value="video" onChange={(e) => this.type = e.target.value} />
                </fieldset>
                <ConfirmationButton captionEnabled="Add job to queue" enabled={this.state.confirmationButtonEnabled} onClick={this.addJobButtonClicked.bind(this)} />
                <hr />
                <p>
                <fieldset>
                  <legend>
                    E-Mail notification
                  </legend>
                <input type="checkbox" checked={this.state.useMailNotification} onClick={() => {this.setState({useMailNotification : !this.state.useMailNotification}); this.storeMailAddress(!this.state.useMailNotification); }} />
                <PatternInput value={this.mail} placeholder="E-Mail" enabled={this.state.useMailNotification} pattern={this.pattern} onMatch={v => this.mailPatternMatched(v)} />
                </fieldset>
              </p>
              <div>
                <JobQueue rows={this.state.rows} />
              </div>

            </div>
        );
    }
      
}

export default JobForm;